<template lang="">
  <div class="container">
    <h1 style="font-family: 'Do Hyeon', sans-serif;"> </h1>
    <table class="table table-bordered">
      <tr>
        <th></th>
        <td>{{house.house.aptName}}</td>
      </tr>

      <tr>
        <th></th>
        <td>{{house.house.city}} {{house.house.gugun}} {{house.house.dong}}</td>
      </tr>

      <tr>
        <th></th>
        <td>{{house.house.dealAmount}},000</td>
      </tr>

      <tr>
        <th></th>
        <td>{{house.house.floor}}</td>
      </tr>

      <tr>
        <th></th>
        <td>{{house.house.area|areafilter}}</td>
      </tr>

      <tr>
        <th></th>
        <td>{{house.house.buildYear}}</td>
      </tr>

      </table>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex';

export default {
  data(){
    return{
      a:'',
    }
  },
  computed:{
    ...mapGetters(["house"]),
  }
  ,
  props:{
    no:String,
  },
  methods:{
    ...mapActions(["getHouse"]),
    houseInfo(no){
      this.getHouse(no);
    }
  },
  created() {
    this.houseInfo(this.no);
  },
  filters:{
    areafilter(value){
      value = value / 3.3;
      return Math.round(value);
    }
  }
}
</script>
