<template>
  <div class="page-header">
    <div
      class="page-header-image"
      style="background-image: url('img/my/night_login.jpg')"
    ></div>
    <div class="content">
      <div class="container">
        <h2 style="font-family: 'Do Hyeon', sans-serif;"></h2>
        <div class="col-md-5 ml-auto mr-auto">
          <card type="login" plain>
            <div class="container">

              <form>
                <div class="form-group d-flex flex-column">
                  <label for="userid">:</label>
                  <input type="userid" class="form-control" placeholder=" " id="userid" v-model="registerInfo.userid">
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="userpwd">:</label>
                  <input type="password" class="form-control" placeholder=" " id="userpwd" v-model="registerInfo.userpwd">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="username">:</label>
                  <input type="text" class="form-control" placeholder=" " id="username" v-model="registerInfo.username">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="email">:</label>
                  <input type="text" class="form-control" placeholder=" " id="email" v-model="registerInfo.email">
                </div>
                <div class="form-group  d-flex flex-column">
                  <label for="address">:</label>
                  <input type="text" class="form-control" placeholder=" " id="address" v-model="registerInfo.address">
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="age">:</label>
                  <select name="age" id="age" v-model="registerInfo.age">
                    <option value=10>10</option>
                    <option value=20 selected>20</option>
                    <option value=30>30</option>
                    <option value=40>40</option>
                    <option value=50>50</option>
                    <option value=60>60</option>
                  </select>
                </div>
                <div class="form-group d-flex flex-column">
                  <label for="role">:</label>
                  <select name="role" id="role" v-model="registerInfo.role">
                    <option value="user" selected></option>
                    <option value="business" ></option>
                  </select>
                </div>
                
                <button type="submit" class="btn btn-info" @click.prevent="moveSubmit"></button>
              </form>

            </div>
          </card>
        </div>
      </div>
    </div>
    <main-footer></main-footer>
  </div>
</template>
<script>
import { Card, Button, FormGroupInput } from '@/components';
import MainFooter from '@/layout/MainFooter';

export default {
  name: 'login-page',
  bodyClass: 'login-page',
  components: {
    Card,
    MainFooter,
    [Button.name]: Button,
    [FormGroupInput.name]: FormGroupInput
  },
  data() {
    return {
      registerInfo:{
        userid: '',
        userpwd: '',
        username: '',
        email: '',
        address: '',
        age: 20,
        role:'user',
      },
    }
  },
  methods: {
    moveSubmit(){
      if(this.registerInfo.userid.trim() == '' || this.registerInfo.userpwd.trim() == '' ||
        this.registerInfo.username.trim() == '' || this.registerInfo.email.trim() == '' ||
        this.registerInfo.address.trim() == ''){
          alert('   .')
        }else{
          this.$store.dispatch("postRegister", this.registerInfo).then((resp) => {
            console.log(resp.data);
            if(resp.data === 'success_business'){
              this.$store.dispatch("addMsg", {status: "business", msg:"    .  ."});
            }else if(resp.data === "success"){
              this.$store.dispatch("addMsg", {status: "success", msg:" !  ."});
            }else{
              this.$store.dispatch("addMsg", {status: "fail", msg:" ..  ."});
            }

            this.$router.push("/login");
          })
        }
    }
  },
};
</script>
<style>
  label{
    text-align: left;
  }
</style>
