<template>
  <div>
    <div class="page-header clear-filter" style="height: 300px;font-family: 'Do Hyeon', sans-serif;">
      <parallax
        class="page-header-image"
        style="background-image:url('/img/my/star_parallax.jpg');height: 300px;"
      >
      </parallax>
      <div class="container" style="height: 300px;">
        <h2 style="color:white"></h2>
      </div>
    </div>
    <div class="section">
      
      <div class="container">
        <h3 class="title">  </h3>
        <form>
          <div class="form-group d-flex flex-column">
            <label for="muserid">:</label>
            <input type="muserid" class="form-control" placeholder=" " id="muserid" v-model="myModifyInfo.info.userid" disabled>
          </div>
          <div class="form-group d-flex flex-column">
            <label for="userpwd">:</label>
            <input type="text" class="form-control" placeholder=" " id="userpwd" v-model="myModifyInfo.info.userpwd">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="username">:</label>
            <input type="text" class="form-control" placeholder=" " id="username" v-model="myModifyInfo.info.username">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="email">:</label>
            <input type="text" class="form-control" placeholder=" " id="email" v-model="myModifyInfo.info.email">
          </div>
          <div class="form-group  d-flex flex-column">
            <label for="address">:</label>
            <input type="text" class="form-control" placeholder=" " id="address" v-model="myModifyInfo.info.address">
          </div>
          <div class="form-group d-flex flex-column">
            <label for="age">:</label>
            <select name="age" id="age" v-model="myModifyInfo.info.age">
              <option value=10>10</option>
              <option value=20 selected>20</option>
              <option value=30>30</option>
              <option value=40>40</option>
              <option value=50>50</option>
              <option value=60>60</option>
            </select>
          </div>  
          <button type="submit" class="btn btn-neutral" style="color:blue" @click.prevent="moveSubmit"></button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
  name: 'profile',
  bodyClass: 'profile-page',
  components: {
  },
  created() {
    this.myModifyInfo.status = {...this.memberLoginInfo.status};
    this.myModifyInfo.info = {...this.memberLoginInfo.info};
    this.myModifyInfo.token = {...this.memberLoginInfo.token};
    console.log(this.myModifyInfo)
  },
  computed:{
    ...mapGetters(['memberLoginInfo'])
  },
  data() {
    return {
      myModifyInfo: {
        status: false,
        info: {},
        token: '',
      },
    }
  },
  methods: {
    moveSubmit(){
      if(this.registerInfo.userid.trim() == '' || this.registerInfo.userpwd.trim() == '' ||
        this.registerInfo.username.trim() == '' || this.registerInfo.email.trim() == '' ||
        this.registerInfo.address.trim() == ''){
          alert('   .')
      }else{
        this.$store.dispatch("putModify", this.myModifyInfo).then((res) => {
          console.log('');
          console.log(res);
          if(res.data.status){
            this.$store.dispatch("setmemberLoginInfo", {
              status: res.data.status,
              info: res.data.data,
              token: res.data.jwt_auto_token
            })
            this.$router.push("/profile");
          }
        })
      }
    }
  },
};
</script>
<style></style>
